<template>
  <div class="user-items">
    <div v-for="item in users" :key="item.id" class="item">
      <img :src="item.avatar_url" />
      <a :href="item.html_url" target="_blank">{{item.login}}</a>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        users: JSON.parse(localStorage.getItem('gitUsers') || '[]')
      }
    },
    mounted() {
      this.$bus.$on('getUsers', data => {
        this.users = data
        localStorage.setItem('gitUsers', JSON.stringify(data))
      })
    },
    beforeDestroy() {
      this.$bus.$off('getUsers')
    }
  }
</script>
<style scoped>
  .user-items {
    display: grid;
    /* grid-row-gap: 12px;
    grid-column-gap: 12px; */
    grid-template-columns: repeat(4, 25%);
    /* grid-template-rows: repeat(3, 33.33%); */
  }
  .item {
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
  }
  .item > img {
    width: 100%;
  }
</style>