<template>
  <div class="count">
    <input class="search-input" type="text" v-model="searchStr" />
    <h1>当前求和: {{sum}}</h1>
    <h4>放大10倍: {{bigSum}}</h4>
    <select v-model.number="num">
      <option value="1">1</option>
      <option value="2">2</option>
      <option value="3">3</option>
    </select>
    <button @click="ONADD(num)">+</button>
    <button @click="ONSUBTRACT(num)">-</button>
    <button @click="onAddOdd(num)">当前求和奇数再加</button>
    <button @click="onAddWait(num)">等一等再加</button>
    <div>定时器{{timerNum}}</div>
  </div>
</template>
<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
export default {
  name: 'Count',
  props: ['initNum'],
  data() {
    console.log(this.initNum)
    return {
      searchStr: '',
      num: Number(this.initNum),
      timerNum: 1
    }
  },
  computed: {
    ...mapState('test', ['sum']),
    ...mapGetters('test', ['bigSum'])
  },
  methods: {
    // increment() {
    //   this.$store.commit('ONADD', this.num)
    // },
    // decrement() {
    //   this.$store.commit('ONSUBTRACT', this.num)
    // },
    ...mapMutations('test',['ONADD', 'ONSUBTRACT']),
    ...mapActions('test', ['onAddOdd', 'onAddWait'])
    // incrementOdd() {
    //   this.$store.dispatch('onAddOdd', this.num)
    // },
    // incrementWait() {
    //   this.$store.dispatch('onAddWait', this.num)
    // }
  },
  mounted() {
    console.log(mapState('test', ['sum']))
  },
  activated() {
    // 被缓存的组件，可以在activated
    this.timer = setInterval(() => {
      this.timerNum += 1
    }, 1000)
  },
  deactivated() {
    clearInterval(this.timer)
  },
  // 通过路由规则，进入该组件时被调用
  beforeRouteEnter(to, from, next) {
    next()
  },
  // 通过路由规则，离开该组件时被调用
  beforeRouteLeave(to, from, next) {
    next()
  }
}
</script>
<style scoped>
  .count {
    width: 60%;
    margin: 20px auto;
  }
  button {
    margin-left: 6px;
  }
</style>