import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const testOptions = {
  namespaced: true,
  actions: {
    onAddOdd(context, data) {
      if (context.state.sum % 2) {
        context.commit('ONADD', data)
      }
    },
    onAddWait(context, data) {
      setTimeout(() => context.commit('ONADD', data), 500)
    }
  },
  mutations: {
    ONADD(state, data) {
      state.sum += data
    },
    ONSUBTRACT(state, data) {
      state.sum -= data
    }
  },
  state: {
    sum: 0
  },
  getters: {
    bigSum(state) {
      return state.sum * 10
    }
  }
}
