<template>
  <div class="search">
    <h2>Search github users</h2>
    <div class="search-warp">
      <input class="search-input" type="text" v-model="searchStr" />
      <button @click="getUsers">搜 索</button>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    props: [ 'onSearch' ],
    data() {
      return {
        searchStr: ''
      }
    },
    methods: {
      getUsers() {
        axios.get(`https://api.github.com/search/users?q=${this.searchStr}`).then(
          response => {
            console.log('请求失败', response.data)
            this.$bus.$emit('getUsers', response.data.items)
          },
          error => {
            console.log('请求失败', error.message)
          }
        )
      }
    }
  }
</script>
<style scoped>
  .search {
    padding: 24px;
    background: #ddd;
  }
  .search-warp {
    display: flex;
  }
  .search-warp > input {
    width: 60%;
    height: 28px;
    padding: 2px 6px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  .search-warp > input:focus {
    outline: none;
  }
</style>