<template>
  <div class="todo-list">
    <AddInput @onAdd="onItemAdd"></AddInput>
    <Todos :datas="todoLs" :onCheck="onItemCheck" :onDelete="onItemDelete"></Todos>
    <Footer :datas="todoLs" ref="footer"></Footer>
  </div>
</template>
<script>
  import AddInput from './AddInput.vue'
  import Todos from './Todos.vue'
  import Footer from './Footer.vue'
  export default {
    name: 'TodoList',
    components: { AddInput, Todos, Footer },
    data() {
      return {
        todoLs: JSON.parse(localStorage.getItem('todos') || '[]')
      }
    },
    methods: {
      onItemAdd(item) {
        this.todoLs.unshift(item)
      },
      onItemCheck(id) {
        this.todoLs.forEach(item => {
          if (item.id === id) {
            item.status = !item.status
          }
        })
      },
      onItemDelete(id) {
        this.todoLs = this.todoLs.filter(item => item.id !== id)
      },
      onCheckAll(status) {
        this.todoLs.forEach(item => {
          item.status = status
        })
      }
    },
    watch: {
      todoLs: {
        deep: true,
        handler() {
          localStorage.setItem('todos', JSON.stringify(this.todoLs))
        }
      }
    },
    mounted() {
      this.$refs.footer.$on('onCheckAll', this.onCheckAll)
    }
  }
</script>
<style scoped>
  .todo-list {
    width: 540px;
    margin: 20px auto;
    padding: 10px;
    overflow: hidden;
    box-shadow: 0px 0px 4px #ddd;
  }
</style>
