<template>
  <div class="footer">
    <label>
      <input type="checkbox" v-model="isAll">
    </label>
    已完成{{doneTotal}} / 
    全部{{total}}
  </div>
</template>
<script>
  export default {
    name: 'Footer',
    props: ['datas'],
    computed: {
      total() {
        return this.datas.length
      },
      doneTotal() {
        return this.datas.reduce((pre, item) => {
          const _temp = item.status ? 1 : 0
          return pre + _temp
        }, 0)
      },
      isAll: {
        get() {
          return this.doneTotal === this.total && this.total > 0
        },
        set(value) {
          this.$emit('onCheckAll', value)
        }
      }
    }
  }
</script>
<style scoped>
  .footer {
    border-top: 1px solid #ddd;
    padding-top: 10px;
    color: #333;
    font-size: 12px;
  }
</style>
