<template>
	<div>
		<input type="text" v-model="name" class="add-input" placeholder="请输入..." @keyup.enter="add" />
	</div>
</template>
<script>
  import { nanoid } from 'nanoid'
  export default {
    name: 'AddInput',
    data() {
      return {
        name: ''
      }
    },
    methods: {
      add() {
        if (!this.name.trim()) {
          return alert('请输入名称')
        }
        this.$emit('onAdd', { id: nanoid(), name: this.name, status: false })
        this.name = ''
      }
    }
  }
</script>
<style scoped>
  .add-input {
    width: -webkit-fill-available;
    height: 28px;
    padding: 2px 6px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  .add-input:focus {
    outline: none;
  }
</style>
